.messegeSender{
    display: flex;
    margin-top: 30px;
    flex-direction: column;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0px 5px 7px -7px rgba(0, 0, 0, 0.75);
    width: 100%;
}

.messegeSender__top{
    display: flex;
    border-bottom: 1px solid #eff2f5;
    padding: 15px;
}

.messegeSender__top > form{
    flex: 1;
    display: flex;
}

 .messegeSender__top > form >input {
    border: none;
    outline-width: 0px;
    padding: 5px 20px;
    margin: 0 10px;
    border-radius: 999px;
    background-color: #eff2f5;
 }
/* 
 @media (max-width: 1200px){
    .messegeSender__top > form >button {
        display: visible;
     }
 } */

 .messegeSender__top > form >button {
    display: none;
 }

 .messegeSender__input{
    flex: 1;
 }

 .messegeSender__bottom{
    display: flex;
    justify-content: space-evenly;
 }
  
.messegeSender__option{
    padding: 20px;
    display: flex;
    align-items: center;
    color: grey;
    margin: 5px;
}

.messegeSender__option > h3{
    font-size: medium;
    margin-left: 10px;
    cursor: pointer;
}

.messegeSender__option:hover{
    background-color: #eff2f5;
    border-radius: 20px;
}


